import Vue from "vue";
import VueRouter from "vue-router";

import home from "../components/home.vue"; //
import member from "../components/member.vue"; //
import detail from "../components/detail.vue"; //
import payment from "../components/payment.vue"; //
import mcMove from "../components/mcMove.vue"; //

import order from "../components/order/orderDetail.vue"; //
import finishOrder from "../components/order/finishOrder.vue"; //
import finishOrder2 from "../components/order/finishOrder2.vue"; //
import unfinishedOrder from "../components/order/unfinishedOrder.vue"; //
import unfinishedOrder2 from "../components/order/unfinishedOrder2.vue"; //

import video from "../components/video.vue";   // 
// import businessman from "../components/businessman.vue";  //
// import wallet from "../components/wallet.vue";  //
// import walletDetail from "../components/walletDetail.vue";  //
// import walletDraw from "../components/walletDraw.vue";  //

// const home = () => import('../components/home.vue'); //
// const member = () => import('../components/member.vue'); //
// const detail = () => import('../components/detail.vue'); //
// const payment = () => import('../components/payment.vue'); //
// const mcMove = () => import('../components/mcMove.vue');//
// const video = () => import('../components/video.vue');// 

const businessman = () => import('../components/businessman.vue'); //

// const order = () => import('../components/order/orderDetail.vue');//
// const finishOrder = () => import('../components/order/finishOrder.vue');//
// const finishOrder2 = () => import('../components/order/finishOrder2.vue');//
// const unfinishedOrder = () => import('../components/order/unfinishedOrder.vue');//
// const unfinishedOrder2 = () => import('../components/order/unfinishedOrder2.vue');//
const refund = () => import('../components/order/refund.vue');//
const refund2 = () => import('../components/order/refund2.vue');//

const wallet = () => import('../components/wallet/wallet.vue');//
const walletDetail = () => import('../components/wallet/walletDetail.vue'); //
const walletDetail2 = () => import('../components/wallet/walletDetail2.vue');//
const walletDraw = () => import('../components/wallet/walletDraw.vue');//
const test = () => import('../components/test.vue');//test  
const test2 = () => import('../components/test2.vue');//test  


Vue.use(VueRouter);

export default new VueRouter({

  // mode: 'history',
  routes: [
    //
    {
      path: "/",
      redirect: "/home"
    },
    //
    {
      path: "/home",
      component: home
    },
    //
    {
      path: "/member/:price/:minutes",
      component: member
    },
    //
    {
      path: "/detail",
      component: detail
    },
    //
    {
      path: "/mcMove/:price/:minutes/:orderId",
      component: mcMove
    },
    //
    {
      path: "/payment",
      component: payment
    },
    //
    {
      path: "/video",
      component: video
    },

    //
    {
      path: "/businessman",
      component: businessman
    },

    //
    {
      path: "/order",
      component: order,
      children: [
        //
        {
          path: '/finishOrder',
          component: finishOrder
        },
        {
          path: '/finishOrder2',
          component: finishOrder2
        },
        //
        {
          path: '/unfinishedOrder',
          component: unfinishedOrder
        },
        //
        {
          path: '/unfinishedOrder2',
          component: unfinishedOrder2
        }
      ]
    },
    //
    {
      path: "/refund",
      component: refund
    },
    {
      path: "/refund2",
      component: refund2
    },

    //
    {
      path: "/wallet",
      component: wallet
    },
    //
    {
      path: "/walletDetail",
      component: walletDetail
    },
    //
    {
      path: "/walletDetail2",
      component: walletDetail2
    },
    //
    {
      path: "/walletDraw/:money",
      component: walletDraw
    },
    {
      path: "/test",
      component: test
    },
    {
      path: "/test2",
      component: test2
    }
  ]
});



