import bannerHeader from "./header";
// import member from "./member";
// import "video.js/dist/video-js.css";
// import { videoPlayer } from "vue-video-player";

export default {
  props: {
    code: String
  },
  data() {
    return {
      home: [],
      checked: "checked",
      val: "1",
      flag: false,
      flc: false,
      isShow: false,
      user: {},
      appId: "",
      timestamp: "",
      nonceStr: "",
      signature: "",
      currentTime: "",
      orderId: " ",
      jsApiList: [],
      isFirst: true,
      investor: false
      // playerOptions: {
      //   //        playbackRates: [0.7, 1.0, 1.5, 2.0], //
      //   autoplay: false, //true,
      //   muted: false, // 
      //   loop: false, // 
      //   preload: "auto", // <video>auto,
      //   language: "zh-CN",
      //   aspectRatio: "16:9", //  - "16:9""4:3"
      //   fluid: true, // trueVideo.js player
      //   sources: [
      //     {
      //       type: "video/mp4",
      //       src: "../../static/introduce.mp4" //m3u8
      //     }
      //   ],
      //   // poster: "poster.jpg", //
      //   width: document.documentElement.clientWidth,
      //   notSupportedMessage: "" //Video.js
      //   //        controlBar: {
      //   //          timeDivider: true,
      //   //          durationDisplay: true,
      //   //          remainingTimeDisplay: false,
      //   //          fullscreenToggle: true  //
      //   //        }
      // }
    };
  },

  created() {
    // const equipmentCode = this.code;
    // alert(equipmentCode);
    // console.log(this.$route.query.code);

    //
    this.axios.get(this.api + "/wx/getUser?code=" + this.code).then(res => {
      console.log(res);
      if (res.data.statu == 1) {
        this.investor = res.data.investor;
        this.user = res.data.user;
        this.isFirst = res.data.user.firstpay;
      } else {
        window.location = this.api + "/wx/login?goback=home?code=" + this.code;
      }
    });

    //
    // this.axios
    //   .get(this.api + "/wx/getMoneyPackage?code=" + this.code)
    //   .then(res => {
    //     console.log("-------------------");
    //     console.log(res);
    //     console.log("-------------------");
    //     this.home = res.data.data;

    //     //,
    //     // if (this.home.length < 2) {
    //     //   this.isFirst = false;
    //     // }
    //   });

    //
    this.axios.get("/api/home").then(res => {
      this.home = res.data.data;
    });

    this.axios
      .post(this.api + "/wx/getConf", {
        path: this.api + "/#" + this.$route.path
      })
      .then(res => {
        // console.log("------------------");
        // console.log(res);
        // console.log("------------------");
        this.appId = res.data.conf.appId;
        this.timestamp = res.data.conf.timestamp;
        this.nonceStr = res.data.conf.nonceStr;
        this.signature = res.data.conf.signature;
        this.jsApiList = res.data.conf.jsApiList;
        wx.config({
          debug: false, // ,apialertpclogpc
          appId: this.appId, // 
          timestamp: this.timestamp, // 
          nonceStr: this.nonceStr, // 
          signature: this.signature, // 1
          jsApiList: this.jsApiList // JSJS2
        });
      });
  },

  // computed: {
  //   player() {
  //     return this.$refs.videoPlayer.player;
  //   }
  // },

  mounted() {},

  methods: {
    // onPlayerPlay(player) {
    //   console.log("play");
    // },
    // onPlayerPause(player) {
    //   console.log("pause");
    // },

    //
    isCheck() {
      if (this.checked == "checked") {
        this.checked = "";
        this.val = "0";
        this.flag = false;
        this.flc = true;
      } else {
        this.checked = "checked";
        this.val = "1";
        this.flag = true;
        this.flc = false;
      }
    },
    close() {
      this.flag = !this.flag;
    },
    play() {
      this.$router.push({
        path: "/video"
      });
      // this.flag = !this.flag;
    },
    loading() {
      this.isShow = true;
    },

    show() {
      this.isShow = false;
    },

    //
    choosePay(item) {
      const equipmentCode = this.code;
      if (this.val != "1") {
        this.loading();
        return;
      }

      this.axios
        .post(this.api + "/wx/getPay", {
          time: item.time,
          money: item.price,
          code: equipmentCode
        })
        .then(res => {
          console.log(res);
          //
          if (res.data.statu == 2) {
            this.$router.push({
              path:
                "/mcMove/" + item.price + "/" + item.time + "/" + this.orderId
            });
          } else if (res.data.statu == 1) {
            //
            if (res.data.statu == 1) {
              this.orderId = res.data.order_id;
              wx.chooseWXPay({
                timestamp: res.data.conf.timestamp, // jssdktimestamptimeStampS
                nonceStr: res.data.conf.nonceStr, //  32 
                package: res.data.conf.package, // prepay_idprepay_id=\*\*\*
                signType: res.data.conf.signType, // 'SHA1''MD5'
                paySign: res.data.conf.paySign, // 

                // 
                success: res => {
                  this.$router.push({
                    path:
                      "/mcMove/" +
                      item.price +
                      "/" +
                      item.time +
                      "/" +
                      this.orderId
                  });
                }
              });
            }
          } else {
            console.log("");
            return;
            console.log(res);
          }
        });

      // if (this.investor == true) {
      //   this.$router.push({
      //     path: "/mcMove/" + item.price + "/" + item.time + "/" + this.orderId
      //   });
      // } else {
      //   this.axios
      //     .post(this.api + "/wx/getPay", {
      //       time: item.time,
      //       money: item.price,
      //       code: equipmentCode
      //     })
      //     .then(res => {
      //       if (res.data.statu == 1) {
      //         this.orderId = res.data.order_id;
      //         wx.chooseWXPay({
      //           timestamp: res.data.conf.timestamp, // jssdktimestamptimeStampS
      //           nonceStr: res.data.conf.nonceStr, //  32 
      //           package: res.data.conf.package, // prepay_idprepay_id=\*\*\*
      //           signType: res.data.conf.signType, // 'SHA1''MD5'
      //           paySign: res.data.conf.paySign, // 

      //           // 
      //           success: res => {
      //             this.$router.push({
      //               path:
      //                 "/mcMove/" +
      //                 item.price +
      //                 "/" +
      //                 item.time +
      //                 "/" +
      //                 this.orderId
      //             });
      //           }
      //         });
      //       }
      //     });
      // }
    }
  },
  components: {
    // member,
    bannerHeader
    // videoPlayer
  }
};



